.AppointmentPage-image {
  /* The image used */
  background-image: url(../Appointments/AppointmentImage.png);

  /* Full height */
  min-height: 100vh;
  /* Center and scale the image nicely */
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: fixed;
  background-repeat: repeat;
  background-size: cover;
  background-color: rgba(0,0,0,0.5); 
  z-index: 10;
}
.MH-Dialog {
  /* The image used */

  /* Full height */
  min-height: 100vh;
  /* Center and scale the image nicely */
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: fixed;
  background-repeat: repeat;
  background-size: cover;
  background-color: #e0e0e0; 
  width: 98%;
  margin: auto;
}
.AppointmentPage-overlay {
  min-height: 100vh;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}
.AppointmentPage-SmallBoxOverLay {
  height: 100%;
  margin-right: 5%;
  margin-left: 5%;
  background-color: rgba(255, 255, 255, 0.507);
}
.AppointmentPage-newInPanel{
    align-content: space-around;
    margin-left: 20%;
}
.AppointmentHistory-h1 {
  color: white;
  animation: flyin 1.5s ease forwards;
  opacity: 0;
  transform: scale(2);
  filter: blur(4px);
}

    
    @keyframes flyin {
       to { 
         filter: blur(0);
         transform: scale(1);
         opacity: 1;
       }
  }